import React from 'react';
import './App.css';

function CountList(props) {
  const { count, item, minus, plus, reset, deleteRow } = props;

  return (
    <div className="list-group-item">
      <button type="button" className="btn" onClick={() => minus(item)}>-1</button>
      <span>{count}</span>
      <button type="button" className="btn" onClick={() => plus(item)}>+1</button>
      <button type="button" className="btn" onClick={() => reset(item)}>Reset</button>
      <button type="button" className="btn" onClick={() => deleteRow(item)}>Delete</button>
    </div >
  );
}

export default CountList;
