import React, { useState } from 'react';
import CountList from './CountList';

import './App.css';

function App() {
  const [count, setCount] = useState([0]);

  const addCounter = () => {
    setCount([...count, 0])
  };

  const minus = (item) => {
    const updateCounter = [...count];
    updateCounter[item] -= 1;
    setCount(updateCounter);
  };

  const plus = (item) => {
    const updateCounter = [...count];
    updateCounter[item] += 1;
    setCount(updateCounter);
  };

  const reset = (item) => {
    const updateCounter = [...count];
    updateCounter[item] = 0;
    setCount(updateCounter);
  };

  const deleteRow = (item) => {
    setCount(count.filter((el, i) => i !== item));
  };

  const resetAll = () => {
    setCount(count.map(el => 0));
  };

  const deleteAll = () => {
    setCount([0]);
  };

  return (
    <div>
      <h3>AddCounters</h3>
      <h6>You can add only 10 counters!</h6>
      {count.map((el, index) => <CountList
        count={el}
        key={index}
        item={index}
        minus={minus}
        plus={plus}
        reset={reset}
        deleteRow={deleteRow}
      />
      )}
      <div>
        <button type="button" className="btn" onClick={addCounter} disabled={count.length > 9}>Add Counter</button>
        <button type="button" className="btn" onClick={resetAll}>Reset all</button>
        <button type="button" className="btn" onClick={deleteAll}>Delete all</button>
      </div>
    </div>
  );
}

export default App;
